import { Direction } from "../../../Direction";
import { PreferredAxis } from "../..";
import { layoutAxis } from "./layoutAxis";
export var commitRootlikeLayout = function (painter, node, lineThickness, bodySize) {
    if (node.siblings().getLayoutPreference() === PreferredAxis.HORIZONTAL ||
        node.siblings().getLayoutPreference() === PreferredAxis.PERPENDICULAR) {
        // Root-like, so just lay out both axes.
        if (layoutAxis(painter, node, Direction.BACKWARD, Direction.FORWARD, !node.neighbors().hasNode(Direction.UPWARD) &&
            !node.neighbors().hasNode(Direction.DOWNWARD), lineThickness, bodySize)) {
            return true;
        }
        // This node is root-like, so it lays out the second-axis children in
        // the same method as the first axis.
        if (layoutAxis(painter, node, Direction.UPWARD, Direction.DOWNWARD, true, lineThickness, bodySize)) {
            return true;
        }
    }
    else {
        // Root-like, so just lay out both axes.
        if (layoutAxis(painter, node, Direction.UPWARD, Direction.DOWNWARD, !node.neighbors().hasNode(Direction.BACKWARD) &&
            !node.neighbors().hasNode(Direction.FORWARD), lineThickness, bodySize)) {
            return true;
        }
        // This node is root-like, so it lays out the second-axis children in
        // the same method as the first axis.
        if (layoutAxis(painter, node, Direction.BACKWARD, Direction.FORWARD, true, lineThickness, bodySize)) {
            return true;
        }
    }
    return false;
};
