import { Direction, nameDirection } from "./Direction";
import { findPaintGroup } from "./DirectionNode/PaintGroups/findPaintGroup";
export function paintGroupBounds(nodeRoot) {
    if (!nodeRoot.neighbors().isRoot() &&
        !nodeRoot.paintGroups().isPaintGroup()) {
        throw new Error("Node must be a paint group");
    }
    var node = nodeRoot;
    var parentSize = [NaN, NaN];
    var groupBounds = {};
    // let numNodes = 0;
    do {
        // ++numNodes;
        node = node.siblings().next();
        node.value().size(parentSize);
        var parentBounds = {
            left: parentSize[0] / 2,
            top: parentSize[1] / 2,
            right: parentSize[0] / 2,
            bottom: parentSize[1] / 2,
        };
        groupBounds[node.id()] = parentBounds;
        var order = node.siblings().layoutOrder();
        for (var i = 0; i < order.length; ++i) {
            var dir = order[i];
            if (dir === Direction.OUTWARD || dir === Direction.INWARD) {
                continue;
            }
            if (!node.neighbors().hasChildAt(dir)) {
                continue;
            }
            var child = node.neighbors().nodeAt(dir);
            if (findPaintGroup(child) === nodeRoot) {
                // Node is part of the same paint group.
                var childBounds = groupBounds[child.id()];
                if (!childBounds) {
                    throw new Error("Child paint group bounds must have" +
                        " been calculated before its parent");
                }
                if (Number.isNaN(childBounds.left)) {
                    throw new Error("Bounds must not be NaN");
                }
                var neighbor = node.neighbors().at(dir);
                switch (dir) {
                    case Direction.UPWARD:
                        parentBounds.top = Math.max(parentBounds.top, childBounds.top + neighbor.separation());
                        parentBounds.left = Math.max(parentBounds.left, childBounds.left - neighbor.alignmentOffset());
                        parentBounds.right = Math.max(parentBounds.right, childBounds.right + neighbor.alignmentOffset());
                        parentBounds.bottom = Math.max(parentBounds.bottom, childBounds.bottom - neighbor.separation());
                        break;
                    case Direction.DOWNWARD:
                        parentBounds.top = Math.max(parentBounds.top, childBounds.top - neighbor.separation());
                        parentBounds.left = Math.max(parentBounds.left, childBounds.left - neighbor.alignmentOffset());
                        parentBounds.right = Math.max(parentBounds.right, childBounds.right + neighbor.alignmentOffset());
                        parentBounds.bottom = Math.max(parentBounds.bottom, childBounds.bottom + neighbor.separation());
                        break;
                    case Direction.FORWARD:
                        parentBounds.top = Math.max(parentBounds.top, childBounds.top - neighbor.alignmentOffset());
                        parentBounds.left = Math.max(parentBounds.left, childBounds.left - neighbor.separation());
                        parentBounds.right = Math.max(parentBounds.right, childBounds.right + neighbor.separation());
                        parentBounds.bottom = Math.max(parentBounds.bottom, childBounds.bottom + neighbor.alignmentOffset());
                        break;
                    case Direction.BACKWARD:
                        parentBounds.top = Math.max(parentBounds.top, childBounds.top - neighbor.alignmentOffset());
                        parentBounds.left = Math.max(parentBounds.left, childBounds.left + neighbor.separation());
                        parentBounds.right = Math.max(parentBounds.right, childBounds.right - neighbor.separation());
                        parentBounds.bottom = Math.max(parentBounds.bottom, childBounds.bottom + neighbor.alignmentOffset());
                        break;
                    default:
                        throw new Error("Unexpected node direction: " + nameDirection(dir));
                }
            }
            else {
                // Node is part of a different paint group.
                var neighbor = node.neighbors().at(dir);
                switch (dir) {
                    case Direction.UPWARD:
                        parentBounds.top = Math.max(parentBounds.top, parentSize[1] / 2 + neighbor.lineLength());
                        break;
                    case Direction.DOWNWARD:
                        parentBounds.bottom = Math.max(parentBounds.bottom, parentSize[1] / 2 + neighbor.lineLength());
                        break;
                    case Direction.FORWARD:
                        parentBounds.right = Math.max(parentBounds.right, parentSize[0] / 2 + neighbor.lineLength());
                        break;
                    case Direction.BACKWARD:
                        parentBounds.left = Math.max(parentBounds.left, parentSize[0] / 2 + neighbor.lineLength());
                        break;
                    default:
                        throw new Error("Unexpected node direction: " + nameDirection(dir));
                }
            }
        }
    } while (node !== nodeRoot);
    // console.log(
    //   nodeRoot,
    //   "Bounds in " + numNodes + " nodes", groupBounds[node._id]);
    return groupBounds[node.id()];
}
