import { findDistance } from "./findDistance";
export var findClosestPaintGroup = function (inserted, paintGroupCandidates) {
    // Compute distances from the inserted node
    var paintGroupDistances = paintGroupCandidates.map(function (candidateNode) {
        return findDistance(inserted, candidateNode);
    });
    var closestPaintGroupIndex = paintGroupDistances.reduce(function (lowestDistanceIndex, candDistance, index) {
        if (lowestDistanceIndex === -1) {
            return index;
        }
        if (candDistance <= paintGroupDistances[lowestDistanceIndex]) {
            return index;
        }
        return lowestDistanceIndex;
    }, -1);
    return paintGroupCandidates[closestPaintGroupIndex];
};
