import { pathToRoot } from "./pathToRoot";
export var findDistance = function (node, other) {
    if (node === other) {
        return 0;
    }
    var nodePath = pathToRoot(node).reverse();
    var otherPath = pathToRoot(other).reverse();
    // Find count in common
    var numCommon = 0;
    for (numCommon = 0; numCommon < Math.min(otherPath.length, nodePath.length); ++numCommon) {
        if (otherPath[numCommon] !== nodePath[numCommon]) {
            break;
        }
    }
    if (numCommon === 0) {
        return Infinity;
    }
    var rv = nodePath.length - numCommon + (otherPath.length - numCommon);
    return rv;
};
