import { reverseDirection } from "../../Direction";
import { pathToRoot } from "./pathToRoot";
/**
 * Returns true if the given other node comes before this node in layout order.
 *
 * @param {DirectionNode} node - subject DirectionNode.
 * @param {DirectionNode} other - object DirectionNode.
 * @return {boolean} true if this node comes before the given node.
 *
 * @see {@link comesAfter}
 */
export var comesBefore = function (node, other) {
    if (node === other) {
        return false;
    }
    if (node.neighbors().isRoot()) {
        // Root comes before all nodes.
        return true;
    }
    if (other.neighbors().isRoot()) {
        // If we are not root, but other is, then other
        // is assumed to come after us.
        return false;
    }
    var nodePath = pathToRoot(node).reverse();
    var otherPath = pathToRoot(other).reverse();
    // Find count in common
    var numCommon = 0;
    for (numCommon = 0; numCommon < Math.min(otherPath.length, nodePath.length); ++numCommon) {
        if (otherPath[numCommon] !== nodePath[numCommon]) {
            break;
        }
    }
    --numCommon;
    if (numCommon < 0) {
        return false;
    }
    var lastCommonParent = nodePath[numCommon];
    if (lastCommonParent === node) {
        return true;
    }
    if (lastCommonParent === other) {
        return false;
    }
    var paintOrdering = lastCommonParent.siblings().layoutOrder();
    var findPaintIndex = function (nodes) {
        return paintOrdering.indexOf(reverseDirection(nodes[numCommon + 1].neighbors().parentDirection()));
    };
    var nodePaintIndex = findPaintIndex(nodePath);
    var otherPaintIndex = findPaintIndex(otherPath);
    return nodePaintIndex < otherPaintIndex;
};
